body {
  background-color: #11122f;
  height: auto;
  min-height: 100%; }

.Layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100%; }
  .Layout_is-show-left-sidebar .Layout__center {
    width: calc(100% - 64px - 316px - 16px); }
  .Layout__inner {
    flex-grow: 1;
    display: flex; }
  .Layout__center {
    position: relative;
    width: calc(100% - 316px - 8px);
    display: flex;
    flex-direction: column;
    height: auto; }
  .Layout__header {
    margin-bottom: 8px;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    background: linear-gradient(182.99deg, #17183A 16.12%, #1E1E47 38.25%, #17183A 66.27%);
    min-height: 65px; }
  .Layout__content {
    width: 100%;
    max-height: unset;
    height: 100%; }
    .Layout__content_blurred {
      filter: blur(10px); }
  .Layout__left {
    width: 64px;
    min-width: 64px;
    margin-right: 8px;
    background: linear-gradient(264.83deg, #17183A 16.12%, #1E1E47 38.25%, #17183A 66.27%); }
  .Layout__right {
    width: 316px;
    min-width: 316px;
    margin-left: 8px;
    background: linear-gradient(242.05deg, #17183A 16.12%, #1E1E47 38.25%, #17183A 66.27%); }
  @media screen and (max-width: 1024px) {
    .Layout__center {
      width: 100%; } }
