.LandingPage__main {
  font-size: 28.8px; }
  .LandingPage__main button {
    cursor: pointer; }
  .LandingPage__main .base-button {
    font-style: normal;
    font-weight: bold;
    font-size: 17px;
    line-height: 21px;
    border: none;
    width: 220px;
    border-radius: 30px;
    padding: 18px 0;
    background-color: #00FFB4; }
    .LandingPage__main .base-button.alt {
      background-color: #fff;
      border: 1px solid #6121a1; }

.LandingPage__first-part {
  height: 380px; }
  @media screen and (max-width: 768px) {
    .LandingPage__first-part {
      height: 330px; } }

.LandingPage__second-part {
  background-color: #F2F2F2;
  position: relative;
  min-height: 420px;
  height: 100%; }

.LandingPage__usdn-logo {
  width: 41px;
  height: 41px; }

.LandingPage__txt-body {
  padding-top: 28px;
  text-align: center;
  width: 100%;
  margin: auto; }
  .LandingPage__txt-body > span:first-child {
    font-style: normal;
    font-weight: 800;
    line-height: 32px; }
  .LandingPage__txt-body > :nth-child(2) {
    font-size: .58em;
    line-height: 31px;
    margin-top: 12px; }

@media screen and (min-width: 1024px) {
  .LandingPage__txt-body {
    max-width: 903px; } }

@media screen and (max-width: 1024px) {
  .LandingPage__txt-body {
    font-size: .9em;
    max-width: 608px; }
    .LandingPage__txt-body > :nth-child(2) {
      margin-top: 35px;
      line-height: 28px; } }

@media screen and (max-width: 768px) {
  .LandingPage__txt-body {
    font-size: .9em;
    max-width: 608px;
    padding-top: 22px; }
    .LandingPage__txt-body > :nth-child(1) {
      text-align: left;
      display: block;
      width: 217px;
      padding-left: 20px;
      font-size: 28px; }
    .LandingPage__txt-body > :nth-child(2) {
      display: none; } }

.LandingPage__romb {
  position: absolute;
  width: 244px;
  height: 244px;
  background-color: #fff;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg) scale(0.89); }
  @media screen and (max-width: 768px) {
    .LandingPage__romb {
      transform: translate(-50%, -50%) rotate(45deg) scale(0.7); } }

.LandingPage__inner-romb {
  width: 184px;
  height: 184px;
  left: 12%;
  top: 12%;
  position: absolute;
  box-shadow: 4px 16px 29px #E3DBF3; }

.LandingPage__romb-text {
  color: black;
  position: absolute;
  transform: rotate(-45deg);
  top: 33%;
  left: 15%;
  display: flex;
  flex-direction: row;
  width: 150px; }
  .LandingPage__romb-text > :nth-child(1) {
    display: flex; }
    .LandingPage__romb-text > :nth-child(1) span {
      margin: auto; }
  .LandingPage__romb-text > :nth-child(2) {
    display: block;
    margin: 10px 6px; }
  .LandingPage__romb-text > :nth-child(3) {
    margin: auto 0; }

.LandingPage__abs-boxes {
  position: absolute;
  left: 50%;
  margin: auto;
  width: max-content;
  transform: scale(0.89); }
  .LandingPage__abs-boxes > img {
    position: relative; }
  .LandingPage__abs-boxes_top {
    transform: translate(-50%, -100%); }
    .LandingPage__abs-boxes_top > :nth-child(1) {
      left: 42px; }
    .LandingPage__abs-boxes_top > :nth-child(2) {
      right: 42px;
      transform: rotate(180deg); }
  .LandingPage__abs-boxes_bottom {
    transform: translate(-50%, 0%); }
    .LandingPage__abs-boxes_bottom > :nth-child(1) {
      left: 42px;
      transform: rotate(180deg); }
    .LandingPage__abs-boxes_bottom > :nth-child(2) {
      right: 42px; }
  @media screen and (max-width: 768px) {
    .LandingPage__abs-boxes_top {
      transform: translate(-50%, -84%) scale(0.7); }
    .LandingPage__abs-boxes_bottom {
      transform: translate(-50%, -15%) scale(0.7); } }

.LandingPage__action-buttons {
  text-align: center;
  padding-top: 154px;
  max-width: 488px;
  width: 100%;
  margin: auto;
  justify-content: space-between;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }
  .LandingPage__action-buttons > * {
    margin: auto;
    color: black !important; }
  @media screen and (max-width: 438px) {
    .LandingPage__action-buttons > :not(:first-child) {
      margin-top: 12px; } }
  .LandingPage__action-buttons > :nth-child(1):hover {
    color: #fff !important; }
  .LandingPage__action-buttons > :nth-child(2):hover {
    background-color: #D0DBFF !important;
    border: 1px solid #D0DBFF !important; }

.LandingPage__mobile-info {
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  display: none; }
  @media screen and (max-width: 768px) {
    .LandingPage__mobile-info {
      display: block; }
      .LandingPage__mobile-info > p {
        color: black;
        margin: 28px 20px;
        text-align: left; }
        .LandingPage__mobile-info > p > :nth-child(2) {
          margin-top: 32px;
          display: block; } }

.LandingPage__tos {
  padding: 12px 0;
  font-style: normal;
  font-weight: 300;
  font-size: .41em;
  line-height: 24px;
  text-align: center; }
  .LandingPage__tos a {
    color: #000000; }
    .LandingPage__tos a:hover {
      text-decoration: underline; }
  @media screen and (max-width: 768px) {
    .LandingPage__tos {
      margin: auto;
      margin-left: 20px; }
      .LandingPage__tos > :nth-child(1) {
        display: block;
        width: max-content; } }

.LandingPage__soc-links {
  margin: auto;
  display: flex;
  flex-direction: row;
  width: 250px;
  justify-content: space-between; }
  .LandingPage__soc-links > *:hover {
    opacity: .8; }
  @media screen and (max-width: 768px) {
    .LandingPage__soc-links {
      margin-left: 20px; } }

.LandingPage__powered-by-waves {
  width: 115px;
  margin: auto;
  padding: 34px 0; }
