.LandingHeader__main {
  font-size: .58em;
  height: 77px;
  background-color: transparent;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  border-bottom: 1px solid #1F1D31; }
  .LandingHeader__main li {
    list-style-type: none;
    font-weight: 600; }
  .LandingHeader__main a:not(:hover) {
    color: white; }

.LandingHeader__burger {
  display: none;
  margin: 46px 20px; }
  @media screen and (max-width: 768px) {
    .LandingHeader__burger {
      display: block; } }

.LandingHeader__sub-dp {
  background: #11122f;
  position: absolute;
  top: 60px;
  border: 1px solid #25233C;
  border-radius: 7px; }
  .LandingHeader__sub-dp > ul:nth-child(1) {
    padding: 22px 20px;
    margin: 0; }
    .LandingHeader__sub-dp > ul:nth-child(1) > li:not(:first-child) {
      padding-top: 16px; }
  .LandingHeader__sub-dp_products {
    left: -60px; }
  .LandingHeader__sub-dp_learn {
    left: 100px; }

.LandingHeader__logo {
  margin: auto;
  margin-left: 120px;
  margin-right: 0;
  font-size: .78em; }
  .LandingHeader__logo > :nth-child(2) {
    margin: 12px 5px;
    display: block;
    float: right; }
  @media screen and (max-width: 768px) {
    .LandingHeader__logo {
      margin-left: 25px;
      margin-top: 35px; } }

.LandingHeader__actions {
  margin: auto;
  margin-right: 120px;
  position: relative; }
  @media screen and (max-width: 768px) {
    .LandingHeader__actions {
      display: none; } }

.LandingHeader__links {
  display: flex;
  flex-direction: row;
  width: 300px;
  justify-content: space-between; }

.LandingHeader__h-link > :nth-child(2) {
  padding: 0 8px;
  transition: transform .15s linear; }

.LandingHeader__h-link_opened > :nth-child(2) {
  transform: rotate(180deg); }

.LandingHeader__mobile-menu {
  position: absolute;
  background: #fff;
  color: black;
  padding: 20px;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  min-height: 616px;
  width: 260px;
  z-index: 2; }
  .LandingHeader__mobile-menu a {
    color: black !important; }
  .LandingHeader__mobile-menu ul {
    padding: 0; }
    .LandingHeader__mobile-menu ul > li {
      margin-top: 24px; }
